import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import background from "./assets/fondo.png";
import Button from 'react-bootstrap/Button';
import { Link } from "react-router-dom";

function Info() {
    return (
        <Container fluid style={{ backgroundImage: `url(${background})`, backgroundPosition:'center top', backgroundSize: '100% 100%' , height: '100vh' ,opacity:'1'}}>
            <Row className="justify-content-md-center">
                <Card style={{ backgroundColor: 'rgba(255,255,255,0.1)',width: '37rem' ,opacity:'1', paddingTop:'1rem'}}>
                <Card.Body style={{ opacity:'1'}}>
                <Row className="justify-content-center">
                <Col >                
                    <Link to="../info">
                        <Button style={{backgroundColor: 'rgba(255,255,255,0.1)', border: '2px solid #ffffff' , width:'10rem', height:'7rem', marginTop:'1rem'}} >
                            <h5 style={{color: 'white', marginTop:'3rem',fontFamily: 'synebold'}}>Ofertas</h5>
                        </Button>
                    </Link>
                </Col>
                <Col >
                    <Link to="../info">
                        <Button style={{backgroundColor: 'rgba(255,255,255,0.1)', border: '2px solid #ffffff' , width:'10rem', height:'7rem', marginTop:'1rem'}} >
                            <h5 style={{color: 'white', marginTop:'3rem',fontFamily: 'synebold'}}>Directorio</h5>
                        </Button>
                    </Link>
                </Col>
                <Col>
                    <Link to="../info">
                        <Button style={{backgroundColor: 'rgba(255,255,255,0.1)', border: '2px solid #ffffff' , width:'10rem', height:'7rem', marginTop:'1rem'}} >
                            <h5 style={{color: 'white', marginTop:'3rem',fontFamily: 'synebold'}}>Recursos</h5>
                        </Button>
                    </Link>
                </Col>
                </Row>
                </Card.Body>
                </Card>
            </Row>
        </Container>
    );
}

export default Info;
