import React, { useState } from 'react';
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Stack from 'react-bootstrap/Stack';
import Form from 'react-bootstrap/Form';
import { Link } from "react-router-dom";

  function Auth(){

    const [count, setCount] = useState(true);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [send, setSend] = useState(false);

    const auth = getAuth();
    if(send){
      const sendAuth = signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        window.location.href = "http://localhost:3000/#/alertas"
      })
      .catch((error) => {
        const errorMessage = error.message;
        console.log(errorMessage);
      });
  
    }


    return(
       <Container>
         <Row style={{paddingTop: '4rem'}}></Row>
         <Row className='justify-content-center' style={{paddingTop: '4rem'}}>
          <Col></Col>
         {count &&
              <Col>
              <Card style={{ width: '30rem' }} className='justify-content-center'>
              <Card.Body>
                <Card.Title className="text-center">Queremos asegurarnos que eres tu</Card.Title>
                <Card.Text>
                  
                </Card.Text>
                <Stack gap={2} className="col-md-5 mx-auto">
                  <Button onClick={() => setCount(!count)} variant="secondary">ingresar</Button>
                  <a className="text-center">no estoy registradx</a>
                </Stack>
              </Card.Body>
            </Card>
            </Col>
         }
         {!count &&
           <Form style={{ width: '30rem' }}>
           <Form.Group className="mb-3" controlId="exampleForm.ControlInput1" onChange={e => setEmail(e.target.value)}>
             <Form.Label>Acá va el email con el que te registraste</Form.Label>
             <Form.Control type="email" placeholder="name@example.com" />
           </Form.Group>
           <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1" onChange={e => setPassword(e.target.value)}>
             <Form.Label>Contraseña</Form.Label>
             <Form.Control type="text" />
           </Form.Group>
           <Button onClick={() => setSend(true)} variant="secondary">Estoy listx</Button>
         </Form>
         }
          <Col></Col>
         </Row>
         <Row></Row>

       </Container>
    )
  }

export default Auth;