import React, { useState } from 'react';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import background from "./assets/fondo.png";
import informacion from "./assets/informacion.png";
import noticias from "./assets/Noticias.png";
import logo from "./assets/logo.png";
import { Link } from "react-router-dom";


function Head() {

    const [login, setLogin] = useState(false);


    if(login){
        window.location.href = "http://localhost:3000/#/app"
    }

  return (
    <Container fluid style={{ backgroundImage: `url(${background})`, backgroundPosition:'center top', backgroundSize: '100% 100%' , height: '100vh'}} >
        <Row className='justify-content-center' style={{paddingTop: '4rem'}}>
            <Col></Col>
            <Col className='text-center'>
                 <p style={{color: 'white' , fontFamily: 'syne', fontSize:'10vw'}}>vitsi</p>
            </Col>
            <Col></Col>
        </Row>        
        <Row style={{paddingTop: '2rem'}}>
            <h3 className='text-center' style={{color: 'white' , fontFamily: 'metropolis'}}>la policia no me cuida, me cuidan mis amigxs</h3> 
        </Row>
        <Row style={{paddingTop: '2rem'}}>
            <Col className='text-center'>
            <Link to="../info"><button style={{backgroundColor: 'rgba(0,0,0,0)', border: '2px solid #ffffff' , width:'25vw' , height:'35vh'}}>
                <Row>
                    <Col></Col>
                    <Col>
                         <img src={informacion} style={{width: '10vw', marginTop:'1rem'}} alt="Logo" />
                    </Col>
                    <Col></Col>
                </Row>
                <Row className='justify-content-center'>
                    <h5 style={{color: 'white', marginTop:'3rem',fontFamily: 'synebold'}}>Información</h5>
                </Row>
                </button>
                </Link>
            </Col>
            <Col className='text-center'>
              <button style={{backgroundColor: 'rgba(0,0,0,0)', border: '2px solid #ffffff', width:'25vw', height:'35vh'}}>
                <Row>
                    <Col></Col>
                    <Col>
                         <img src={noticias} style={{width: '14vw', marginTop:'1rem'}} alt="Logo" />
                    </Col>
                    <Col></Col>
                </Row>
                <Row className='justify-content-center'>
                    <h5 style={{color: 'white', marginTop:'3rem',fontFamily: 'synebold'}}>Noticias</h5>
                </Row>
                </button>
            </Col>
            <Col className='text-center'>
            <Link to="../app"><button style={{backgroundColor: 'rgba(0,0,0,0)', border: '2px solid #ffffff' , width:'25vw', height:'35vh'}} >
                <Row>
                    <Col></Col>
                    <Col>
                         <img src={logo} style={{width: '17vw', marginTop:'1rem'}} alt="Logo" />
                    </Col>
                    <Col></Col>
                </Row>
                <Row className='justify-content-center'>
                    <h5 style={{color: 'white', marginTop:'3rem',fontFamily: 'synebold'}}>App</h5>
                </Row>
                </button>
                </Link>
            </Col>
        </Row>
    </Container>
  );
}

export default Head;

